                    key: 'addr_type',
                    width: 150
                },
                {
                    title: '地址',
                    key: 'address'
                },
                {
                    title: '余额（HT）',
                    sortable : true,
                    key: 'balance'
                },
                {
                    title: '操作',
                    key: 'action',
                    fixed: 'right',
                    width: 120,
                    render: (h, params) => {
